import { Block, Node } from '@ali/4ever-cangjie';

export function isTableCell(maybeTableCell: Node): maybeTableCell is Block {
  return Block.isBlock(maybeTableCell) && maybeTableCell.type === 'table-cell';
}

export function isTableRow(maybeTableRow: Node): maybeTableRow is Block {
  return Block.isBlock(maybeTableRow) && maybeTableRow.type === 'table-row';
}

export function isTable(maybeTable: Node): maybeTable is Block {
  return Block.isBlock(maybeTable) && maybeTable.type === 'table';
}
